@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,400;0,700;1,400&display=swap);
html {
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
}

*, ::after, ::before {
    box-sizing: inherit;
    -webkit-font-smoothing: inherit;
}

body {
    font-family: 'Dela Gothic One', cursive;
    background-color: #005857;
    margin: 0px;
    background-image: url(/converter-react/static/media/doodle.12130957.jpg);
    background-size: 750px;
    background-position: center;
}

a {
    text-decoration: none;
}
/* converter.css */

.converter {
    width: 100%;
    min-width: 400px;
    max-width: 600px;
    margin: auto;
    text-align: center;
    font-size: 20px;
    color: #005857;
    background-color: #FFF;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 0 10px 10px #00000099;
}

.converter--hidden {
    display: none;
}

.converter__header {
    padding: 0 0 20px 0;
}

.converter__select {
    font-family: 'Dela Gothic One', cursive;
    width: 150px;
    text-align: center;
    border: #000 solid 2px;
    border-radius: 10px;
    padding: 10px;
    background-color: #FFF;
    color: #005857;
}

.converter__select:active {
    background-color: #005857;
    color: #FFF;
}

.converter__select:focus {
    background-color: #005857;
    color: #FFF;
}

.converter__field {
    font-family: 'Dela Gothic One', cursive;
    width: 250px;
    text-align: center;
    border: #000 solid 2px;
    border-radius: 10px;
    padding: 10px;
    background-color: #FFF;
    color: #005857;
}

.converter__field:active {
    background-color: #ebebeb;
}

.converter__field:disabled {
    background-color: #005857;
    color: #DDD;
}
/* header.css */
.header__header {
    text-align: center;
    color: #FFF;
    text-shadow: 0 0 10px #000;
}
